import React, { useEffect, useState } from "react";
import profilePic from "../assets/images/nic/Chor24-3-cropped.jpg";

const About: React.FC = () => {

    return (
        <div className="about-container">
            {/* Bio Section with Image */}
            <img src={profilePic} alt="Nic" className="profile-pic" />
            <section className="bio-section">
                
                <div className="bio-text">
                    <h1>Nic Schilling</h1>
                    <p>
                        Nic Schilling wurde am 05.10.1996 in Buchen (Odenwald) geboren. Nach zahlreichen Umzgen und einer grtenteils autodidaktischen musikalischen Bildung produzierte dey mit 16 Jahren deren erstes eigenes Pop-Album Rise.<br />
                        Im Alter von 17 Jahren komponierte Nic deren erstes Musiktheaterstck Laika, ein Jugendmusical. Dieses wurde in der Deutschen Schule Budapest im Jahr 2014 unter deren Leitung zur Auffhrung gebracht. Nach dem Abitur verbrachte dey ein Jahr als Produktions- und Regieassistenz im Palast der Knste (MPa) Budapest.<br />
                        Nic erlangte 2021 an der Universitt Bayreuth einen Bachelor of Arts im Fach Musiktheaterwissenschaft und absolvierte zeitgleich im Rahmen eines Gaststudiums die C-Prfung im Fach Chorleitung an der Hochschule fr evangelische Kirchenmusik Bayreuth. Seit 2021 studiert dey an der Universitt im Master Computerspielwissenschaft M.Sc.<br />
                        Neben diversen kompositorischen Arbeiten schrieb und inszenierte Nic auch das Live-Hrspiel Scherben, komponierte das Musical 'Im Auge des Sturms' und leitete verschiedene studentische Musiktheater-Produktionen. 2019 verbrachte dey vier Monate in Nicosia auf Zypern, wo dey als Musikalische Assistenz von Alex Panayi die Komposition und Urauffhrung des Musicals      begleitete. Whrend des Master-Studiums arbeitete dey vermehrt an dynamischer Musikproduktion fr Spiele und leitete den Unichor Bayreuth.<br />
                        Anfang 2023 grndete Nic gemeinsam mit Ruben Schfer das Studio 'Intellumia' fr die Produktion interaktiver, pdagogischer Installationen.<br />
                    </p>
                </div>
            </section>
        </div>
    );
};

export default About;